export default { 
  name: 'VTabs',
  props: {
    value: {
      type: [String, Number],
      default: 0
    },
    height: {
      type: String,
      default: '45px'
    },
    tabs: {
      type: Array,
      default() {
        return [];
      }
    },
    backgroundColor: {
      type: String,
      default: 'transparent'
    },
    borderRadius: {
      type: String,
      default: '5px'
    },
    color: {
      type: String,
      default: '#333333'
    },
    activeColor: {
      type: String,
      default: '#007AFF'
    },
    fontSize: {
      type: String,
      default: '14px'
    },
    activeFontSize: {
      type: String,
      default: '14px'
    },
    lineScale: {
      type: Number,
      default: 0.6
    },
    lineHeight: {
      type: String,
      default: '3px'
    },
    lineColor: {
      type: String,
      default: '#007AFF'
    }
  },
  data() {
    return {
      scrollLeft: 0,
      activeTab: 0,
      width: 0,
      lineLeft: 0,
      lineWidth: 0
    };
  },
  watch: {
    value(newVal) {
      this.$emit('value', newVal);
    }
  },
  methods: {
    handleTapItem(e) {
      this.activeTab = e.currentTarget.dataset.index;
      this.getTabsWidth(e.currentTarget.offsetLeft);
    },
    getTabsWidth(offsetLeft) {
      const query = uni.createSelectorQuery().in(this);
      query
        .select('.v-tabs')
        .boundingClientRect(data => {
          this.width = data.width;
        })
        .exec();
      setTimeout(() => {
        query
          .select('.v-tabs__item.active')
          .boundingClientRect(data => {
            console.log(data);
            this.lineLeft = offsetLeft + (data.width * (1 - this.lineScale)) / 2;
            this.lineWidth = data.width * this.lineScale;
            this.scrollLeft = offsetLeft - (this.width - data.width) / 2;
          })
          .exec();
      }, 10);
    }
  },
  mounted() {
    this.activeTab = this.value;
    this.getTabsWidth(0);
  }
};
